    <q-card class="my-card" square style="width: 770px; height: 560px">
      <q-separator />

      <q-card-actions vertical>
        <div class="q-pa-md" >
          <q-table
            title="Table"
            :data="data"
            :columns="columns"
            row-key="id"
            :pagination.sync="pagination"
            :loading="loading"
            :filter="filter"
            @request="onRequest"
            binary-state-sort
            
            table-style="height:400px"
           
          >
            <template v-slot:top-right>
              <q-input
                borderless
                dense
                debounce="300"
